div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  font-size: 28px;
  font-weight: bold;
  background: linear-gradient(135deg, #ff0000, #cc0000);
  color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgba(255, 0, 0, 0.5);

}